.auth{
    width: auto;
    background-color: rgb(253, 249, 244);
    border: 1px solid #708090;
    border-radius: 0px 5px 5px 0px;
    padding-left: 7%;
    padding-right: 7%;
    padding-bottom: 5%;
}

.authContent{
    display: grid;
    justify-content: center;
    gap: 10px;
}

.authContent h2{
    margin-left: 2%;
    font-size: 44px;
}

.authContent label{
    font-size: 20px;
    margin-right: 10px;
}

.authContent button{
    padding: 5px;
    border-radius: 5px;
    background-color: aliceblue;
    outline: none;
    border: 1px solid slategray;
    font-size: 20px;
    margin-left: 80px;
    color: #404952;
}



    @media (max-width:714px){
        .auth{
            border-radius: 5px 5px 5px 5px;
        }
    }